body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#clock {
  margin-top: 2rem;
  height: 100vh;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-auto-rows: auto;
}

#heading {
  grid-column: span 2;
}
#break-container {
  display: flex;
  justify-content: center;
}

#session-container {
  display: flex;
  justify-content: center;
}

#break-length,
#session-length {
  font-size: 2.5rem;
}

#break-container > button,
#session-container > button {
  margin: 0px 15px;
  background-color: white;
  border: 0px;
  font-size: 1.5rem;
}

#timer-container {
  grid-column: span 2;
  font-size: 2.5rem;
}

#time-left {
  display: flex;
  justify-content: center;
}

#start_stop,
#reset {
  margin: 0px 10px;
}
